
import ShopBtn from '@/components/shopBtn'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'

import { getSKUActiveList, submitPreOrder } from '@/api/order'
import noData from '@/components/noData.vue'

export default {
name: 'shoppingtrolley',
data () {
return {
demo1: '',
orderDetail: {},
checkList: [],
deltoast: false,
value: 0,
orcheck: '',
show: false,
noshop: false,
productinfo: [], // 商品信息
postData: {// 当前购物车数据
jsonstr: ''
},
submitData: {// 提交结算数据
jsonstr: ''
},
clickProduct: {}, // 点击时的商品
totalmoney: 0,
presentnamefilter: []
}
},
/*
computed: {
noshop (val) {
if (this.productinfo.length > 0) {
return false
}
return true
}
}, */
created () {
this.getLocalCar()

},
watch: {
checkList (val) {

this.calcMoney(val, this.productinfo)
if (val.length === this.productinfo.length) {
this.checkall = []
this.orcheck = ''
this.orcheck = 'all'
let newArr = []
this.productinfo.map(function (item, index, input) {
newArr.push(index)
})
return newArr
} else {
this.orcheck = ''
}
},
productinfo: {
handler (curVal, oldVal) {

curVal.map(item => {

if (item.presentname !== '') {
this.presentnamefilter = item.presentname.split(',')
return
}

})

for(var m=0;m<curVal.length;m++)
{
if (curVal[m].allactivitylist.length <= 0)
{//没有可用活动
}
else
{

var act_normaltotalprice = curVal[m].num * curVal[m].defaultoneprice;
var act_promoney = 0;
var selectactid = -1;
var selecttotal = 0;

for(var n=0;n<curVal[m].allactivitylist.length;n++)
{

var act_canbuynum = curVal[m].num;
var act_normonnum = 0;

var act_content = curVal[m].allactivitylist[n]['content'];
if ( 1 * act_content['limittotal'] > 0 )
{//有限量总量
if ((1 * act_content['limittotal'] - 1 * curVal[m].allactivitylist[n]['limittotaled']) < act_canbuynum)
{//可买数量不足
act_canbuynum = 1 * act_content['limittotal'] - 1 * curVal[m].allactivitylist[n]['limittotaled'];
act_normonnum = curVal[m].num - act_canbuynum;
}
}
if ( 1 * act_content['limitnum'] > 0 )
{//有限购个人量
if ((1 * act_content['limitnum'] - 1 * curVal[m].allactivitylist[n]['limitnumed']) < act_canbuynum)
{//可买数量不足
act_canbuynum = 1 * act_content['limitnum'] - 1 * curVal[m].allactivitylist[n]['limitnumed'];
act_normonnum = curVal[m].num - act_canbuynum;
}
}


var thispromtotal = 0;
var thistotal = 0;
var tmpprice = 0;
var eachprice1 = 0;
var eachprice2 = 0;
switch(1*act_content['template_id'])
{
case 1 :

if (!act_content['unifyprice'])
{//单个价,找到对应价格
var eachpricelist = act_content['eachpricelist'];
eachpricelist = eachpricelist.replace(/\[/g, "");
eachpricelist = eachpricelist.replace(/\]/g, "");
var eacharr = eachpricelist.split(',');
eacharr.forEach(function(value,index,array){
var oneitem = value.split(':');
if (oneitem[0] == curVal[m].skuid)
{
tmpprice = 1 * oneitem[1];
return false;
}
});
}
else
{//统一价
tmpprice = 1 * act_content['unifyprice'];
}
thistotal = act_canbuynum * tmpprice + act_normonnum * curVal[m].defaultoneprice;
thispromtotal = act_normaltotalprice - thistotal;

break;

case 2 :

if (!act_content['unifyprice'])
{//单个价,找到对应价格
var eachpricelist = act_content['eachpricelist'];
eachpricelist = eachpricelist.replace(/\[/g, "");
eachpricelist = eachpricelist.replace(/\]/g, "");
var eacharr = eachpricelist.split(',');
eacharr.forEach(function(value,index,array){
var oneitem = value.split(':');
if (oneitem[0] == curVal[m].skuid)
{
tmpprice = 1 * oneitem[1];
return false;
}
});
}
else
{//统一价
tmpprice = 1 * act_content['unifyprice'];
}
thistotal = act_canbuynum * tmpprice + act_normonnum * curVal[m].defaultoneprice;
thispromtotal = act_normaltotalprice - thistotal;

break;

case 3 :

var eachpricelist = act_content['eachprice1'];
eachpricelist = eachpricelist.replace(/\[/g, "");
eachpricelist = eachpricelist.replace(/\]/g, "");
var eacharr = eachpricelist.split(',');
eacharr.forEach(function(value,index,array){
var oneitem = value.split(':');
if (oneitem[0] == curVal[m].skuid)
{
eachprice1 = 1 * oneitem[1];
return false;
}
});
eachpricelist = act_content['eachprice2'];
eachpricelist = eachpricelist.replace(/\[/g, "");
eachpricelist = eachpricelist.replace(/\]/g, "");
eacharr = eachpricelist.split(',');
eacharr.forEach(function(value,index,array){
var oneitem = value.split(':');
if (oneitem[0] == curVal[m].skuid)
{
eachprice2 = 1 * oneitem[1];
return false;
}
});
tmpprice = (1 * eachprice1 + 1 * eachprice2);
thistotal = act_canbuynum * tmpprice + act_normonnum * curVal[m].defaultoneprice;
thispromtotal = act_normaltotalprice - thistotal;

break;

case 4 :

var eachpricelist = act_content['eachprice1'];
eachpricelist = eachpricelist.replace(/\[/g, "");
eachpricelist = eachpricelist.replace(/\]/g, "");
var eacharr = eachpricelist.split(',');
eacharr.forEach(function(value,index,array){
var oneitem = value.split(':');
if (oneitem[0] == curVal[m].skuid)
{
eachprice1 = 1 * oneitem[1];
return false;
}
});
eachpricelist = act_content['eachprice2'];
eachpricelist = eachpricelist.replace(/\[/g, "");
eachpricelist = eachpricelist.replace(/\]/g, "");
eacharr = eachpricelist.split(',');
eacharr.forEach(function(value,index,array){
var oneitem = value.split(':');
if (oneitem[0] == curVal[m].skuid)
{
eachprice2 = 1 * oneitem[1];
return false;
}
});
tmpprice = (1 * eachprice1 + 1 * eachprice2);
thistotal = act_canbuynum * tmpprice + act_normonnum * curVal[m].defaultoneprice;
thispromtotal = act_normaltotalprice - thistotal;

break;

case 5 :

thistotal = (act_canbuynum + act_normonnum) * curVal[m].defaultoneprice;
thispromtotal = act_canbuynum * curVal[m].allactivitylist[n]['moneyparam'];

break;

case 6 :

if (!act_content['unifydiscount'])
{//单个价,找到对应价格
var eachpricelist = act_content['eachdiscountlist'];
eachpricelist = eachpricelist.replace(/\[/g, "");
eachpricelist = eachpricelist.replace(/\]/g, "");
var eacharr = eachpricelist.split(',');
eacharr.forEach(function(value,index,array){
var oneitem = value.split(':');
if (oneitem[0] == curVal[m].skuid)
{
tmpprice = 1 * oneitem[1];
return false;
}
});
}
else
{//统一价
tmpprice = 1 * act_content['unifydiscount'];
}

thistotal = (act_canbuynum * tmpprice / 100 + act_normonnum) * curVal[m].defaultoneprice;
thispromtotal = act_normaltotalprice - thistotal;

break;

case 7 :

thistotal = (act_canbuynum + act_normonnum) * curVal[m].defaultoneprice;
thispromtotal = act_canbuynum * curVal[m].allactivitylist[n]['moneyparam'];

break;

case 8 :

thistotal = (act_canbuynum * curVal[m].allactivitylist[n]['moneyparam'] / 100 + act_normonnum) * curVal[m].defaultoneprice;
thispromtotal = act_normaltotalprice - thistotal;

break;

}



if ((thispromtotal > act_promoney)||(1*act_content['template_id']==3)||(1*act_content['template_id']==4))
{
selectactid = n;
selecttotal = thistotal;
act_promoney = thispromtotal;
}

}

if (selectactid != -1)
{
var tmpcontent = curVal[m].allactivitylist[selectactid]['content'];
curVal[m].activityid = tmpcontent['template_id'];
curVal[m].activityname = tmpcontent['title'];
curVal[m].template_id = 1*tmpcontent['template_id'];

curVal[m].price = (selecttotal / curVal[m].num).toFixed(2);
}
}


}

this.updateLocal(curVal)
if (curVal.length > 0) {
this.noshop = false
} else {
this.noshop = true
}
this.calcMoney(this.checkList, curVal)
},
deep: true
}
},
components: {
CheckerItem,
Checker,
ShopBtn,
XNumber,
noData
},
methods: {
close: function () {
this.show = false
},
handleCheck (val) {
var tmpcheckList = []
let _this = this
this.productinfo.map(function (item, index, input) {
if(item.skuid === val.skuid){
if(_this.checkList.indexOf(index) !== -1){

return
}
tmpcheckList.push(index)
}
})
this.checkList = this.checkList.concat(tmpcheckList)
},
/*
nocarshop (){
let ShoppingCarnum = window.localStorage.getItem('ShoppingCarStr')
let ShoppingCarStrnum = JSON.parse(ShoppingCarnum)
if(ShoppingCarStrnum===null){
this.noshop = true
}
}, */
spec: function (index) { // 打开附加服务选择框
this.show = true
this.clickProduct.img = this.productinfo[index].picurl
this.clickProduct.price = this.productinfo[index].price
this.clickProduct.productname = this.productinfo[index].productname + ' ' + this.productinfo[index].skuname
this.clickProduct.additionist = this.productinfo[index].additionist
this.clickProduct.index = index
},
gotoDetail: function (id) {
this.$router.push({path: '/orderDetail', query: { skuid: id}})
},

calcMoney: function (sellist, plist) {
var totalmoney = 0
var prolist = plist

sellist.forEach(function (item, index, input) {

totalmoney += 1 * prolist[item].price * prolist[item].num
prolist[item].additionist.forEach(function (a, b, c) {
a.productlist.forEach(function (x, y, z) {
if (x.isselected) {
totalmoney += 1 * x.price * prolist[item].num
}
})
})
})

this.totalmoney = totalmoney.toFixed(2)
},

delseled () {
var sellist = this.checkList
for (var i = sellist.length - 1; i >= 0; i--) {
this.productinfo.splice(sellist[i], 1)
}
this.checkList = []
},

topaybtn () {
var prolist = this.productinfo
var sellist = this.checkList

var outarr = new Array()
for (var i = 0; i < sellist.length; i++) {
var arr = new Object()
arr.skuid = prolist[sellist[i]].skuid
arr.num = prolist[sellist[i]].num
var addlist = new Array()
prolist[sellist[i]].additionist.forEach(function (a, b, c) {
a.productlist.forEach(function (x, y, z) {
if (x.isselected) {
var obj = new Object()
obj.addid = x.id
addlist.push(obj)
}
})
})
arr.addlist = addlist
outarr.push(arr)
}
this.submitData.jsonstr = JSON.stringify(outarr)
this.submitOrderData()
},
clickaddbtn: function (i, j) { // 附加服务被点中

if (this.clickProduct.additionist[i].productlist[j].isselected == false) { // 选中自己，同时清除其它所有
this.clickProduct.additionist[i].productlist.forEach(function (item, index, input) {
input[index].isselected = false
})
this.clickProduct.additionist[i].productlist[j].isselected = true
} else { // 非选自己
this.clickProduct.additionist[i].productlist[j].isselected = false
}
},
editorderbtn () {
this.deltoast = true
},
editsuccessbtn () {
this.deltoast = false
},
allcheck () {
var tmpcheckList = []
if (this.orcheck === '') {
this.productinfo.map(function (item, index, input) {
tmpcheckList.push(index)
})
}
this.checkList = tmpcheckList
},
getLocalCar () {


this.postData.jsonstr = window.localStorage.getItem('ShoppingCarStr')
if (this.postData.jsonstr) {
this.getRemoteData()
} else {
this.noshop = true
}
},
updateLocal (newdata) {
var outarr = new Array()
newdata.forEach(function (item, index, input) {
var arr = new Object()
arr.skuid = item.skuid
arr.num = item.num
var addlist = new Array()
var j = 0
item.additionist.forEach(function (a, b, c) {
a.productlist.forEach(function (x, y, z) {
if (x.isselected) {
var obj = new Object()
obj.addid = x.id
addlist.push(obj)
}
})
})
arr.addlist = addlist
outarr.push(arr)
})

window.localStorage.setItem('ShoppingCarStr', JSON.stringify(outarr))
},
getRemoteData () {

getSKUActiveList(this.postData)
.then(res => {
if (res.data.errorcode === 0) {

this.productinfo = res.data.data

} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
submitOrderData () {
submitPreOrder(this.submitData)
.then(res => {
if (res.data.errorcode === 0) {


this.$router.push({path: '/confirmOrder', query: { preid: res.data.data.preid}})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
}
}
}



